import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.less']
})
export class UserComponent implements OnInit {

  userName: string;

  age: number;

  email: string;

  address: Address;

  hobbies: string[];

  hobbie: string;

  isEdit: boolean = true;

  users: string[];

  constructor(private dataService: DataService) {
    this.users = this.dataService.getUsers();
  };

  ngOnInit() {
    this.userName = "liuze";
    this.age = 22;
    this.email = "2411509324@qq.com";
    this.address = {
      city: '昆明市',
      state: '寻甸县',
      street: '马街'
    };
    this.hobbies = [
      "敲代码",
      "看书",
      "听音乐"
    ];
  };

  addHobbie(): boolean {
    if (this.hobbie) {
      this.hobbies.push(this.hobbie);
      this.hobbie = '';
    }
    return false;
  };

  removeHobbie(index: number): void {
    this.hobbies.splice(index, 1);
  };

  toggleEdit(): void {
    this.isEdit = !this.isEdit;
  };

};

interface Address {
  street: string;
  city: string;
  state: string;
};
