import { Injectable } from '@angular/core';

import { HttpClient } from '@angular/common/http';

import { Observable, from } from 'rxjs/index';

import { Config } from '../model/config';

import { api } from '../model/api';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  private users: string[];

  private jsonUrl: string = api.jsonUrl;

  constructor(private http: HttpClient) {
    this.users = ['liuze', '刘泽', 'Mr.liu'];
  };

  getUsers() {
    return this.users;
  };

  getUsers2(): Observable<any> {
    return from(this.users);
  };

  getJson() {
    return this.http.get<Config[]>(this.jsonUrl);
  };

  addData(user: Config) {
    return this.http.post<Config>(this.jsonUrl, user);
  };

  deleteData(id) {
    // console.log(id);
    return this.http.delete<Config[]>(this.jsonUrl + '/' + id);
  };

  updataData(data) {
    return this.http.put<Config>(this.jsonUrl + '/' + data.id, data);
  };

  getSingleData(id: number) {
    return this.http.get(this.jsonUrl + '/' + id);
  };

};