import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';

import { Config } from '../../model/config';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {

  public users: string[];

  public datas: Config[];

  private isEdit: boolean = false;

  public user: Config = {
    userId: '',
    title: '',
    body: ''
  };

  public dataStatus: boolean = false;

  constructor(public dataService: DataService) {
    this.users = this.dataService.getUsers();
    // console.log(this.dataService.getUsers2());
    // this.dataService.getUsers2().subscribe(user => {
    //   console.log(user);
    // })
    this.dataService.getJson().subscribe((datas: Config[]) => {
      this.datas = datas;
    })
  };

  ngOnInit() { };

  onSubmit(isEdit: boolean): boolean {

    if (isEdit) {
      this.dataService.updataData(this.user).subscribe((data: Config) => {
        for (let i = 0; i < this.datas.length; i++) {
          if (this.datas[i].id === this.user.id) {
            this.datas.splice(i, 1);
          }
        }
        this.datas.unshift(this.user);
        this.user = {
          userId: '',
          title: '',
          body: ''
        };
      });
    } else {
      Object.keys(this.user).forEach(key => {
        // console.log(this.user[key]);
        if (this.user[key]) this.dataStatus = true;
        else this.dataStatus = false;
      })
      if (this.dataStatus) {
        this.dataService.addData(this.user).subscribe((data: Config) => {
          this.datas.unshift(data);
        })
      };
    }
    return false;
  };

  onDeketeClick(id: number) {
    // console.log(id);
    this.dataService.deleteData(id).subscribe((datas: Config[]) => {
      for (let i = 0; i < this.datas.length; i++) {
        if (this.datas[i].id === id) {
          this.datas.splice(i, 1);
        }
      }
    })
  };

  onEditClick(data: Config) {
    this.isEdit = true;
    this.user = data;
  };
};
